#to-top {
  width: 40px;
  height: 40px;
  padding: 7px;
  background-color: white;
  visibility: hidden;
  position: absolute;
  z-index: 1;
  left: 50%;
  top: 90%;
  height: 40px;
  border-radius: 25px;
  -webkit-box-shadow: -1px 11px 39px -13px rgba(0,0,0,0.7);
  -moz-box-shadow: -1px 11px 39px -13px rgba(0,0,0,0.7);
  box-shadow: -1px 11px 39px -13px rgba(0,0,0,0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin-left: -27px;
}
#to-top a {
  text-decoration: none;
  font-size: 10px;
  line-height: 11px;
  color: #4c4c4c;
  font-family: 'Raleway',sans-serif;
