@media screen and (max-width: 959px){
  .left{
    width: 100%;
  }
  .panel{
    width: 98%;
  }
  .panel img{
    width: 200px;
    height: 200px;
  }
  .right{
    width: 100%;
    min-width: 320px;
  }
  .col-3{
    width: 95%;
    clear: both;
    margin-left: 2.5%;
    max-width: 95%;
  }
  .col-2{
    width: 95%;
    clear: both;
    margin-left: 2.5%;
    max-width: 95%;
  }
}

@media screen and (max-width: 768px){

  #sec4 .container{
    width: 95%;
  }
  header{
    height: 50px;
  }
  #sec4 h1{
    font-size: 23px;
  }
  #timeline{
    background: url(img/line.png) repeat-y 0 0;
    padding-top: 20px;

  }
  .tl-year{
    background: url(img/point-big.png) no-repeat 0 0;
    text-align: center;
    margin: 0;
  }
  .tl-event{
    background: url(img/point-small.png) no-repeat top left;
    margin: auto;
    width: 100%;
    overflow: auto;
    visibility: visible;
    margin-left: -20px;
  }
  .tl-event > div{
    width: 90%;
    position: relative;
    float: right;
  }
  .tl-event .event-l{
    background: url(img/arrow-left.png) no-repeat top left;
  }
  .tl-event .event-l div{
    margin-left: 26px;
  }
  .svg{
    width: 40px;
    height: 40px;
  }
}

@media screen and (max-width: 400px){
  .tl-event .event-r{
    margin-right: -0px;
  }
  .right{
    height: 50px;
    width: 300px;
  }
}


